import React, {Component} from 'react';

// props:
//		stopAlarm()


/**
 * I exists to beep
 */
class Alarm extends Component {


	componentDidMount() {
		console.log("Starting");
		// console.log("mounted");
		let pause = 2800;
		let intervalNum = setInterval(e => this.beep(300), pause);
		setTimeout(() => {
			let intervalNum2 = setInterval(e => this.beep(600), pause);
			this.setState({
				id2: intervalNum2
			})
		}, 300);

		setTimeout(() => {
			let intervalNum3 = setInterval(e => this.beep(604), pause); // create some beat frequency
			this.setState({
				id3: intervalNum3
			})
		}, 300);
		
		let ctx = new AudioContext();
		this.setState({
			id: intervalNum,
			context: ctx
		});
	}

	beep = (f) => {
		if (this.state.context === null) {
			return;
		}
		// console.log(this.state.context);

		let o = this.state.context.createOscillator();
		o.type = "sine";
		let g = this.state.context.createGain();
		g.gain.value = 0.15;  // volume control 
		o.connect(g);
		g.connect(this.state.context.destination);

		o.frequency.value = f;

		o.start();
		g.gain.exponentialRampToValueAtTime(.00001, this.state.context.currentTime + 2.8);
	}

	componentWillUnmount() {
		console.log("Stopping");
		clearInterval(this.state.id2);
		clearInterval(this.state.id3);
		clearInterval(this.state.id);
	}

	render() {
		return (
			<div className="Alarm"> 
				<h2>				
					Ringing!!!
				</h2>
				<button onClick={this.props.stopAlarm}>Clear</button>
			</div>
		);
	}
}

export default Alarm;
